import { useEffect, useState } from 'react'
import { getAllProfiles, updateUserRole } from '@/services/admin'
import { UserProfile } from '@/types'
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import { Badge } from '@/components/ui/badge'
import { useToast } from '@/hooks/use-toast'
import { useAuth } from '@/hooks/use-auth'
import { useNavigate } from 'react-router-dom'
import { CreateUserDialog } from '@/components/admin/CreateUserDialog'

export default function UserManagement() {
  const [profiles, setProfiles] = useState<UserProfile[]>([])
  const [loading, setLoading] = useState(true)
  const { toast } = useToast()
  const { user, role } = useAuth()
  const navigate = useNavigate()

  useEffect(() => {
    // Check role, but allow render if super_admin
    // The previous implementation redirected if not super_admin.
    // We should keep this check.
    if (role && role !== 'super_admin' && role !== 'admin') {
      // Allowing 'admin' to view for now, but role changing might be restricted
      // User Story says "As an administrator... control who has access"
    }
  }, [role, navigate])

  const loadProfiles = async () => {
    setLoading(true)
    try {
      const data = await getAllProfiles()
      setProfiles(data || [])
    } catch (error) {
      toast({
        title: 'Error',
        description: 'Failed to load user profiles',
        variant: 'destructive',
      })
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    if (role === 'super_admin' || role === 'admin') {
      loadProfiles()
    }
  }, [role])

  const handleRoleChange = async (userId: string, newRole: string) => {
    if (role !== 'super_admin') {
      toast({
        title: 'Permission Denied',
        description: 'Only Super Admins can change roles',
        variant: 'destructive',
      })
      return
    }

    try {
      await updateUserRole(userId, newRole)
      setProfiles((prev) =>
        prev.map((p) => (p.id === userId ? { ...p, role: newRole as any } : p)),
      )
      toast({ title: 'Success', description: 'User role updated' })
    } catch (error) {
      toast({
        title: 'Error',
        description: 'Failed to update role',
        variant: 'destructive',
      })
    }
  }

  if (role !== 'super_admin' && role !== 'admin') {
    return (
      <div className="flex h-[50vh] items-center justify-center">
        <div className="text-center">
          <h2 className="text-2xl font-bold">Access Denied</h2>
          <p className="text-muted-foreground">
            You do not have permission to view this page.
          </p>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div className="space-y-2">
          <h1 className="text-3xl font-bold">User Management</h1>
          <p className="text-muted-foreground">
            Manage user roles and permissions.
          </p>
        </div>
        <CreateUserDialog onSuccess={loadProfiles} />
      </div>

      <div className="border rounded-md">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>User Info</TableHead>
              <TableHead>Joined</TableHead>
              <TableHead>Role</TableHead>
              <TableHead>Actions</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {loading ? (
              <TableRow>
                <TableCell colSpan={4} className="text-center py-8">
                  <div className="flex justify-center items-center gap-2">
                    <span className="animate-spin rounded-full h-4 w-4 border-b-2 border-primary"></span>
                    Loading users...
                  </div>
                </TableCell>
              </TableRow>
            ) : profiles.length === 0 ? (
              <TableRow>
                <TableCell colSpan={4} className="text-center py-8">
                  No users found.
                </TableCell>
              </TableRow>
            ) : (
              profiles.map((profile) => (
                <TableRow key={profile.id}>
                  <TableCell>
                    <div className="flex flex-col">
                      <span className="font-medium text-sm">
                        {profile.email || 'No email available'}
                      </span>
                      <span className="font-mono text-xs text-muted-foreground">
                        {profile.id}
                        {profile.id === user?.id && (
                          <Badge variant="secondary" className="ml-2 h-4 px-1">
                            You
                          </Badge>
                        )}
                      </span>
                    </div>
                  </TableCell>
                  <TableCell>
                    {new Date(profile.created_at).toLocaleDateString()}
                  </TableCell>
                  <TableCell>
                    <Badge variant="outline">{profile.role || 'user'}</Badge>
                  </TableCell>
                  <TableCell>
                    <Select
                      defaultValue={profile.role || 'user'}
                      onValueChange={(val) => handleRoleChange(profile.id, val)}
                      disabled={
                        profile.id === user?.id || role !== 'super_admin'
                      }
                    >
                      <SelectTrigger className="w-[140px]">
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="user">User</SelectItem>
                        <SelectItem value="editor">Editor</SelectItem>
                        <SelectItem value="admin">Admin</SelectItem>
                        <SelectItem value="super_admin">Super Admin</SelectItem>
                      </SelectContent>
                    </Select>
                  </TableCell>
                </TableRow>
              ))
            )}
          </TableBody>
        </Table>
      </div>
    </div>
  )
}
